<template>
  <div class="login-in">
    <login-logo />
    <div class="login">
      <div class="login-head">
        <span>Login</span>
      </div>
      <el-form
        :model="loginForm"
        status-icon
        :rules="rules"
        ref="loginForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="username">
          <el-input
            placeholder="userName"
            v-model="loginForm.username"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="password"
            v-model="loginForm.password"
            @keyup.enter.native="loginIn"
          ></el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button @click="goSignUp">Register</el-button>
          <el-button type="primary" @click="handleLoginIn">Login</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "../mixins";
import LoginLogo from "../components/LoginLogo";
import { HttpManager } from "../api/index";

export default {
  name: "login-in",
  mixins: [mixin],
  components: {
    LoginLogo
  },
  data: function() {
    let validateName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("User name cannot be empty"));
      } else {
        callback();
      }
    };
    let validatePassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please enter your password"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        // 登录用户名密码
        username: "",
        password: ""
      },
      rules: {
        username: [
          {
            validator: validateName,
            message: "Please enter your username",
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: validatePassword,
            message: "Please enter your password",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.changeIndex("Login");
  },
  methods: {
    changeIndex(value) {
      this.$store.commit("setActiveName", value);
    },
    handleLoginIn() {
      let _this = this;
      let params = new URLSearchParams();
      console.log(this.loginForm.username);
      // params.append("username", "jake");
      params.append("username", this.loginForm.username);
      params.append("password", this.loginForm.password);
      console.log(params);
      HttpManager.loginIn({
        username: this.loginForm.username,
        password: this.loginForm.password
      })
        .then(res => {
          if (res.code === 1) {
            _this.$message({
              message: "Successfully logged in",
              type: "success"
            });
            _this.setUserMsg(res.userMsg[0]);
            _this.$store.commit("setLoginIn", true);
            setTimeout(function() {
              _this.changeIndex("HomePage");
              _this.$router.push({ path: "/" });
              _this.$router.go(0);
            }, 2000);
          } else {
            _this.notify("Wrong user name or password", "error");
          }
        })
        .catch(failResponse => {});
    },
    setUserMsg(item) {
      this.$store.commit("setUserId", item.id);
      this.$store.commit("setUsername", item.username);
      this.$store.commit("setAvator", item.avator);
    },
    goSignUp() {
      this.$router.push({ path: "/sign-up" });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/login-in.scss";
</style>



